<template>
  <div class="item">
            <div>
              <div class="item-img">
                <img
                  :alt="item.name"
                  :src="item.sku_info[indexItem].ali_image+'?x-oss-process=image/resize,w_206/quality,Q_80/format,webp'"
                  style="opacity: 1;"
                />
              </div>
              <h6>{{item.name}}</h6>
              <h3>{{item.name_title}}</h3>
              <div class="params-colors">
                <ul class="colors-list">
                  <li v-for="(item1, index1) in item.sku_info" :key="index1" @click="changeSelect(index1)">
                    <a href="javascript:;" :class="{active:index1===indexItem}" :title="item1.spec_json.show_name">
                      <img
                        :src="'http://img01.smartisanos.cn/'+item1.spec_json.image+'20X20.jpg'"
                      />
                    </a>
                  </li>
                </ul>
              </div>
              <div class="item-btns clearfix">
                <span class="item-gray-btn">
                  <router-link :to="{name:'ItemInfo',query:{itemId:item.sku_info[indexItem].sku_id}}">查看详情</router-link>
                </span>
                <span class="item-blue-btn" @click="addItemHandle(item.sku_info[indexItem])">加入购物车</span>
              </div>
              <div class="item-price clearfix">
                <i>¥</i>
                <span>{{item.price}}</span>
              </div>
              <div class="discount-icon">false</div>
              <div class="item-cover">
                <router-link :to="{name:'ItemInfo',query:{itemId:item.sku_info[indexItem].sku_id}}"></router-link>
              </div>
            </div>
          </div>
</template>

<script>
export default {
  props: {
    item: {
      type: Object
    }
  },
  data () {
    return {
      indexItem: 0,
      count: 1
    }
  },
  methods: {
    changeSelect (index1) {
      this.indexItem = index1
    },
    addItemHandle (item) {
      this.$store.commit('addItem', {info: item, count: 1})
    }
  }
}
</script>

<style>

</style>



