import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex)

let store = new Vuex.Store({
  state: {
    cartPanelItem: [],
    showCart: false,
    showPrompt: false,
    address: [
      {
        'name': '王某某',
        'phone': '13811111111',
        'areaCode': '010',
        'landLine': '64627856',
        'provinceId': 110000,
        'province': '北京市',
        'cityId': 110100,
        'city': '市辖区',
        'countyId': 110106,
        'county': '海淀区',
        'add': '上地十街辉煌国际西6号楼319室',
        'default': true,
        'checked': true
      },
      {
        'name': '李某某',
        'phone': '13811111111',
        'areaCode': '010',
        'landLine': '64627856',
        'provinceId': 110000,
        'province': '北京市',
        'cityId': 110100,
        'city': '市辖区',
        'countyId': 110106,
        'county': '海淀区',
        'add': '上地十街辉煌国际东6号楼350室',
        'default': false,
        'checked': false
      }
    ],
    sumitInfo: {}
  },
  mutations: {
    addItem (state, newItem) {
      let notExist = true
      state.cartPanelItem.forEach(item => {
        if (item.sku_id === newItem.info.sku_id) {
          item.count += newItem.count
          if (item.count > 5) {
            item.count = 5
            state.showPrompt = true
          }
          notExist = false
        }
      })
      if (notExist) {
        Vue.set(newItem.info, 'count', newItem.count)
        state.cartPanelItem.push(newItem.info)
      }
    },
    deItem (state, deItem) {
      state.cartPanelItem.forEach(item => {
        if (item.sku_id === deItem.sku_id) {
          item.count -= 1
        }
      })
    },
    showCart (state) {
      state.showCart = true
    },
    hideCart (state) {
      state.showCart = false
    },
    deleteCartItem (state, ridItem) {
      state.cartPanelItem.forEach((item, index) => {
        if (item.sku_id === ridItem.sku_id) {
          state.cartPanelItem.splice(index, 1)
          return 0
        }
      })
    },
    closePrompt (state) {
      state.showPrompt = false
    },
    setSelect (state) {
      state.cartPanelItem.forEach(item => {
        Vue.set(item, 'checked', true)
      })
    },
    changeSelect (state, checkedItem) {
      state.cartPanelItem.forEach(item => {
        if (item.sku_id === checkedItem.sku_id) {
          item.checked = !item.checked
        }
      })
    },
    setSelectAll (state) {
      if (this.getters.checkSelectAll) {
        state.cartPanelItem.forEach(item => {
          item.checked = false
        })
      } else {
        state.cartPanelItem.forEach(item => {
          item.checked = true
        })
      }
    },
    deleteSelectItem (state) {
      for (let i = state.cartPanelItem.length - 1; i >= 0; i--) {
        if (state.cartPanelItem[i].checked === true) {
          state.cartPanelItem.splice(i, 1)
        }
      }
    },
    saveAddress (state, addressData) {
      state.address.push(addressData)
    },
    sumit (state, data) {
      state.sumitInfo = data
      let i = state.cartPanelItem.length
      while (i--) {
        if (state.cartPanelItem[i].checked === true) {
          state.cartPanelItem.splice(i, 1)
        }
      }
    },
    isPay (state) {
      state.sumitInfo.isPay = true
    },
    payNow (state, itemInfo) {
      if (state.cartPanelItem.some(item => item.sku_id === itemInfo.sku_id)) {
        state.cartPanelItem.forEach(item => {
          if (item.sku_id === itemInfo.sku_id) {
            item.count += 1
            item.checked = true
          } else {
            item.checked = false
          }
        })
      } else {
        state.cartPanelItem.forEach(item => (item.checked = false))
        Vue.set(itemInfo, 'count', 1)
        Vue.set(itemInfo, 'checked', true)
        state.cartPanelItem.push(itemInfo)
      }
    }
  },
  getters: {
    totalCount (state) {
      let count = 0
      state.cartPanelItem.forEach(item => (count += item.count))
      return count
    },
    totalPrice (state) {
      let priceCount = 0
      state.cartPanelItem.forEach(item => {
        priceCount += item.price * item.count
      })
      return priceCount
    },
    hasCartItem (state) {
      return state.cartPanelItem.length
    },
    checkSelectAll (state) {
      return state.cartPanelItem.every(item => item.checked === true)
    },
    selectCount (state) {
      let count = 0
      state.cartPanelItem.forEach(item => {
        if (item.checked === true) {
          count += item.count
        }
      })
      return count
    },
    selectPrice (state) {
      let priceCount = 0
      state.cartPanelItem.forEach(item => {
        if (item.checked === true) {
          priceCount += item.count * item.price
        }
      })
      return priceCount
    }
  }
})

export default store



