<template>
  <div id="main">
    <div class="sku-box store-content">
      <div class="sort-option">
        <ul class="line clear">
          <li>
            <a href="javascript:;" class="active">综合排序</a>
          </li>
          <li>
            <a href="javascript:;" class>销量排序</a>
          </li>
          <li>
            <a href="javascript:;" class>价格低到高</a>
          </li>
          <li>
            <a href="javascript:;" class>价格高到低</a>
          </li>
        </ul>
      </div>
      <div class="gray-box">
        <div class="item-box">
          <show-item v-for="(item, index) in goodsData" :key="index" :item="item"></show-item>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import goodsData from '@/lib/newGoodsData.js'
import showItem from '@/components/show-item'
export default {
  data () {
    return {
      goodsData
    }
  },
  components: {
    showItem
  }
}
</script>

<style>
@import '../assets/css/goodsList.css';
</style>



