<template>
  <div id="main">
    <div class="store-content item">
      <div class="item-box">
        <div class="gallery-wrapper">
          <div class="gallery">
            <div class="thumbnail">
              <ul>
                <li :class="{on:itemIndex===index}" v-for="(item, index) in itemInfo.ali_images" :key="index" @click="changeLook(index)">
                  <img :src="item+'?x-oss-process=image/resize,w_54/quality,Q_90/format,webp'" />
                </li>
              </ul>
            </div>
            <div class="thumb">
              <ul>
                <li :class="{on:itemIndex===index}" v-for="(item, index) in itemInfo.ali_images" :key="index">
                  <img :src="item+'?x-oss-process=image/resize,w_440/quality,Q_90/format,webp'" />
                </li>
              </ul>
            </div>
          </div>
        </div>
        <div class="banner">
          <div class="sku-custom-title">
            <div class="params-price">
              <span>
                <em>¥</em>
                <i>{{itemInfo.price}}</i>
              </span>
            </div>
            <div class="params-info">
              <h4>{{itemInfo.title}}</h4>
              <h6>{{itemInfo.sub_title}}</h6>
            </div>
          </div>
          <div class="sku-dynamic-params-panel">
            <div class="sku-dynamic-params clear">
              <span class="params-name">颜色</span>
              <ul class="params-colors">
                <li :class="{cur:item.id===itemInfo.sku_id}"
                v-for="(item, index) in itemInfo.sku_list" :key="index">
                  <router-link :to="{name: 'ItemInfo',query: {itemId: item.id}}">
                    <i>
                      <img
                        :src="'http://img01.smartisanos.cn/'+item.image+'20X20.jpg'"
                      />
                    </i>
                  </router-link>
                </li>
              </ul>
            </div>
            <div class="sku-dynamic-params clear">
              <div class="params-name">数量</div>
              <div class="params-detail clear">
                <div class="item-num js-select-quantity">
                  <span class="down" :class="{'down-disabled':count===1}" @click="deCountHandle">-</span>
                  <span class="num">{{count}}</span>
                  <span class="up" :class="{'up-disabled':count===5}" @click="addCountHandle">+</span>
                </div>
              </div>
            </div>
          </div>
          <div class="sku-status">
            <div class="cart-operation-wrapper clearfix">
              <span class="blue-title-btn js-add-cart">
                <a @click="addItemHandle">加入购物车</a>
              </span>
              <span class="gray-title-btn">
                <a @click="payNowHandle">现在购买</a>
              </span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import itemsData from '@/lib/newItemsData.js'
export default {
  data () {
    return {
      itemId: this.$route.query.itemId,
      itemIndex: 0,
      count: 1
    }
  },
  watch: {
    '$route.query.itemId' () {
      return (this.itemId = this.$route.query.itemId)
    }
  },
  computed: {
    itemInfo () {
      return itemsData.filter(item => item.sku_id === Number(this.itemId))[0]
    }
  },
  methods: {
    changeLook (index) {
      this.itemIndex = index
    },
    changeColor (item) {
      this.itemId = item.id
    },
    addItemHandle () {
      this.$store.commit('addItem', {info: this.itemInfo, count: this.count})
    },
    addCountHandle () {
      if (this.count < 5) {
        this.count += 1
      }
    },
    deCountHandle () {
      if (this.count > 1) {
        this.count -= 1
      }
    },
    payNowHandle () {
      this.$store.commit('payNow', this.itemInfo)
      this.$router.push({name: 'CheckOut'})
    }
  }
}
</script>

<style>
@import url(../assets/css/item.css);
</style>



