import Vue from 'vue'
import Router from 'vue-router'
import Shop from '@/view/shop'
import ItemInfo from '@/view/item-info'
import Cart from '@/view/cart'
import CheckOut from '@/view/check-out'
import Payment from '@/view/payment'
import Account from '@/view/account'
import Order from '@/view/account/order'
import Address from '@/view/account/address'

Vue.use(Router)

export default new Router({
  mode: 'history',
  routes: [
    {
      path: '/',
      name: 'Shop',
      component: Shop
    },
    {
      path: '/item-info',
      name: 'ItemInfo',
      component: ItemInfo
    },
    {
      path: '/cart',
      name: 'Cart',
      component: Cart
    },
    {
      path: '/check-out',
      name: 'CheckOut',
      component: CheckOut
    },
    {
      path: '/payment',
      name: 'Payment',
      component: Payment
    },
    {
      path: '/account',
      name: 'Account',
      component: Account,
      children: [
        {
          path: 'order',
          name: 'Order',
          component: Order
        },
        {
          path: 'address',
          name: 'Address',
          component: Address
        }
      ]
    }
  ]
})



