<template>
  <div class="nav-cart-wrapper">
    <div class="nav-cart-list">
      <div class="empty" v-if="!this.$store.getters.hasCartItem">
        <h3>购物车为空</h3>
        <p>您还没有选购任何商品，现在前往商城选购吧!</p>
      </div>
      <div class="full">
        <div class="nav-cart-items">
          <ul>
            <li class="clear" v-for="(item, index) in cartItem" :key="index">
              <div class="cart-item js-cart-item cart-item-sell">
                <div class="cart-item-inner">
                  <div class="item-thumb">
                    <img :src="item.ali_image" />
                  </div>
                  <div class="item-desc">
                    <div class="cart-cell">
                      <h4>
                        <a href="#/item/100027401">{{item.title}}</a>
                      </h4>
                      <p class="attrs">
                        <span>{{item.sub_title}}</span>
                      </p>
                      <h6>
                        <span class="price-icon">¥</span>
                        <span class="price-num">{{item.price}}</span>
                        <span class="item-num">x {{item.count}}</span>
                      </h6>
                    </div>
                  </div>
                  <div class="del-btn" @click="deleteCartItemHandle(item)">删除</div>
                </div>
              </div>
            </li>
          </ul>
        </div>
        <div class="nav-cart-total" v-if="this.$store.getters.hasCartItem">
          <p>
            共
            <strong class="ng-binding">{{this.$store.getters.totalCount}}</strong> 件商品
          </p>
          <h5>
            合计：
            <span class="price-icon">¥</span>
            <span class="price-num ng-binding" ng-bind="cartMenu.totalPrice">{{this.$store.getters.totalPrice}}</span>
          </h5>
          <h6>
            <router-link :to="{name:'Cart'}" class="nav-cart-btn">去购物车</router-link>
          </h6>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    cartItem () {
      return this.$store.state.cartPanelItem
    }
  },
  methods: {
    deleteCartItemHandle (item) {
      this.$store.commit('deleteCartItem', item)
    }
  }
}
</script>

<style>
</style>



