<template>
  <div id="header">
    <div class="nav-global">
      <div class="container">
        <h1 class="nav-logo">
          <a href="javascript:;"></a>
        </h1>
        <ul class="nav-aside">
          <li class="nav-user">
            <a href="javascript:;">个人中心</a>
            <!--active-->
            <div class="nav-user-wrapper">
              <div class="nav-user-list">
                <dl class="nav-user-avatar">
                  <dd>
                    <span class="ng-scope"></span>
                  </dd>
                  <dt class="ng-binding">+86 138****9453</dt>
                </dl>
                <ul>
                  <li class="order">
                    <router-link :to="{name: 'Order'}">我的订单</router-link>
                  </li>
                  <li class="address">
                    <a href="javascript:;">收货地址</a>
                  </li>
                  <li class="logout">
                    <a href="javascript:;">退出</a>
                  </li>
                </ul>
              </div>
            </div>
          </li>
          <!--active-->
          <li class="nav-cart" @mouseenter="showCartHandle" @mouseleave="hideCartHandle">
            <a href="javascript:;">购物车</a>
            <!--根据class改变颜色-->
            <span class="cart-empty-num" :class="{'cart-num':this.$store.getters.hasCartItem}">
              <i>{{this.$store.getters.totalCount}}</i>
            </span>
            <cart-wrapper v-if="this.$store.state.showCart"></cart-wrapper>
          </li>
        </ul>
        <ul class="nav-list">
          <li>
            <a href="javascript:;">在线商城</a>
          </li>
          <li>
            <a href="javascript:;">坚果 Pro</a>
          </li>
          <li>
            <a href="javascript:;">Smartisan M1 / M1L</a>
          </li>
          <li>
            <a href="javascript:;">Smartisan OS</a>
          </li>
          <li>
            <a href="javascript:;">欢喜云</a>
          </li>
          <li>
            <a href="javascript:;">应用下载</a>
          </li>
          <li>
            <a href="javascript:;">官方论坛</a>
          </li>
        </ul>
      </div>
    </div>
    <div class="nav-sub">
      <div class="nav-sub-wrapper">
        <div class="container">
          <ul class="nav-list">
            <li>
              <a href="javascript:;">首页</a>
            </li>
            <li>
              <a href="javascript:;">手机</a>
            </li>
            <li>
              <a href="javascript:;">“足迹系列”手感膜</a>
            </li>
            <li class="active">
              <router-link :to="'/'">官方配件</router-link>
            </li>
            <li>
              <a href="javascript:;">周边产品</a>
            </li>
            <li>
              <a href="javascript:;">第三方配件</a>
            </li>
            <li>
              <a href="javascript:;">全部商品</a>
            </li>
            <li>
              <a href="javascript:;">服务</a>
            </li>
          </ul>
        </div>
      </div>
    </div>
    <prompt v-if="this.$store.state.showPrompt"></prompt>
  </div>
</template>

<script>
import cartWrapper from '@/components/cart-wrapper'
import prompt from '@/components/prompt'
export default {
  components: {
    cartWrapper,
    prompt
  },
  methods: {
    showCartHandle () {
      this.$store.commit('showCart')
    },
    hideCartHandle () {
      setTimeout(() => {
        this.$store.commit('hideCart')
      }, 500)
    }
  }
}
</script>

<style>
</style>



