<template>
  <div id="pop">
    <div class="module-dialog-layer" style="display: block;"></div>
    <div class="module-dialog clear module-dialog-address module-dialog-show">
      <div class="dialog-panel">
        <div class="topbar">
          <div class="dialog-tit clear">
            <h4 class="js-dialog-title">管理收货地址</h4>
          </div>
          <span class="dialog-close" @click="close">x</span>
        </div>
        <div class="dialog-con js-dialog-container">
          <div class="animate-layer">
            <div class="dialog-inner js-address-add">
              <div class="save-address-box">
                <div class="address-form">
                  <div class="module-form-row">
                    <div class="form-item-v3" :class="{'form-invalid-item':!nameCheck}">
                      <i v-if="!addressData.name">收货人姓名</i>
                      <input type="text" class="js-verify" v-model="addressData.name" @blur="checkName"/>
                      <div class="verify-error" v-if="!nameCheck">请输入收货人姓名</div>
                    </div>
                  </div>
                  <div class="module-form-row">
                    <div class="form-item-v3" :class="{'form-invalid-item':!phoneCheck}">
                      <i v-if="!addressData.phone">手机号</i>
                      <input type="text" class="js-verify" v-model="addressData.phone" @blur="checkPhone"/>
                      <div class="verify-error" v-if="!phoneCheck">请输入正确手机号</div>
                    </div>
                  </div>
                  <div class="module-form-row clear">
                    <div class="form-item-v3 area-code-w fn-left form-valid-item">
                      <i>区号（可选）</i>
                      <input type="text" class="js-verify js-address-area-code" />
                      <div class="verify-error"></div>
                    </div>
                    <div class="form-item-v3 telephone-w fn-right form-valid-item">
                      <i>固定电话（可选）</i>
                      <input type="text" class="js-verify js-address-telephone" />
                      <div class="verify-error"></div>
                    </div>
                  </div>
                  <div class="module-form-row clear">
                    <div class="form-item-v3 select-item province-wrapper">
                      <select
                        name="province_code"
                        class="province select-province js-form-province js-verify"
                        v-model="addressData.provinceId"
                      >
                        <option value="0">请选择省份</option>
                        <option :value="province.area_id" v-for="(province, index) in provinceList" :key="index">{{province.area_name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="module-form-row clear">
                    <div class="form-item-v3 select-item city-wrapper fn-left form-focus-item">
                      <select class="city select-city js-form-city js-verify" v-model="addressData.cityId">
                        <option value="0">请选择城市</option>
                        <option :value="city.area_id" v-for="(city, index) in cityList" :key="index">{{city.area_name}}</option>
                      </select>
                    </div>
                    <div class="form-item-v3 select-item district-wrapper fn-right form-focus-item">
                      <select class="city select-city js-form-city js-verify" v-model="addressData.countyId">
                        <option value="0">请选择区县</option>
                        <option :value="county.area_id" v-for="(county, index) in countyList" :key="index">{{county.area_name}}</option>
                      </select>
                    </div>
                  </div>
                  <div class="module-form-row">
                    <div class="form-item-v3" :class="{'form-invalid-item':!addressCheck}">
                      <i v-if="!addressData.add">详细地址，如街道名称，楼层，门牌号码等</i>
                      <input type="text" class="js-verify" v-model="addressData.add"/>
                      <div class="verify-error" v-if="!addressCheck">请输入详细地址</div>
                    </div>
                  </div>
                  <div class="module-form-row fn-clear">
                    <input type="checkbox" class="hide" v-model="addressData.default"/>
                    <span class="blue-checkbox blue-checkbox-on"></span>设为默认
                  </div>
                  <div class="dialog-blue-btn big-main-btn js-verify-address" :class="{'disabled-btn':!showSave}">
                    <a @click="saveAddressHandle">保存</a>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import provinceList from '@/lib/addList'
export default {
  data () {
    return {
      provinceList,
      addressData: {
        'name': '',
        'phone': '',
        'areaCode': '010',
        'landLine': '64627856',
        'provinceId': 0,
        'province': '北京市',
        'cityId': 0,
        'city': '市辖区',
        'countyId': 0,
        'county': '海淀区',
        'add': '',
        'default': false,
        'checked': true
      },
      phoneCheck: true,
      nameCheck: true,
      addressCheck: true
    }
  },
  watch: {
    'addressData.provinceId' () {
      this.addressData.cityId = 0
      this.addressData.countyId = 0
    },
    'addressData.cityId' () {
      this.addressData.countyId = 0
    }
  },
  computed: {
    cityList () {
      let city = []
      provinceList.forEach(item => {
        if (item.area_id === this.addressData.provinceId) {
          city = item.city_list
        }
      })
      return city
    },
    countyList () {
      let county = []
      this.cityList.forEach(item => {
        if (item.area_id === this.addressData.cityId) {
          county = item.county_list
        }
      })
      return county
    },
    showSave () {
      if (this.addressData.name && this.addressData.phone && this.phoneCheck && this.addressData.provinceId !== 0 && this.addressData.cityId !== 0 && this.addressData.countyId !== 0 && this.addressData.add) {
        return true
      } else {
        return false
      }
    }
  },
  methods: {
    close () {
      this.$emit('onclose')
    },
    checkPhone () {
      if (this.addressData.phone.length !== 11) {
        this.phoneCheck = false
      } else {
        this.phoneCheck = true
      }
    },
    checkName () {
      if (this.addressData.name) {
        this.nameCheck = true
      } else {
        this.nameCheck = false
      }
    },
    checkAddress () {
      if (this.addressData.add) {
        this.addressCheck = true
      } else {
        this.addressCheck = false
      }
    },
    saveAddressHandle () {
      this.$store.commit('saveAddress', this.addressData)
      this.$emit('onclose')
    }
  }
}
</script>

<style>
@import url(../assets/css/address-pop.css);
</style>



