<template>
  <div id="main" class="hander-car">
    <div class="store-content">
      <div class="cart-box">
        <div class="title">
          <h2>购物清单</h2>
        </div>
        <div class="cart-inner">
          <div class="empty-label" :class="{'hide':cartItem.length}">
            <h3>您的购物车中还没有商品</h3>
            <router-link :to="'/'" class="link">现在选购</router-link>
          </div>
          <div v-if="cartItem.length">
            <div class="cart-table-title">
              <span class="name">商品信息</span>
              <span class="operation">操作</span>
              <span class="subtotal">小计</span>
              <span class="num">数量</span>
              <span class="price">单价</span>
            </div>
            <div class="cart-table">
              <div class="cart-group">
                <!--购物列表-->
                <div class="cart-top-items" v-for="(item, index) in cartItem" :key="index">
                  <div class="cart-items">
                    <div class="items-choose">
                      <span class="blue-checkbox-new" :class="{'checkbox-on':item.checked}" @click="changeSelectHandle(item)">
                        <a></a>
                      </span>
                    </div>
                    <div class="items-thumb">
                      <img :src="item.ali_image" />
                      <a href="javascript:;" target="_blank"></a>
                    </div>
                    <div class="name hide-row">
                      <div class="name-table">
                        <a href="javascript:;" target="_blank">{{item.title}}</a>
                        <ul class="attribute">
                          <li>{{item.sub_title}}</li>
                        </ul>
                      </div>
                    </div>
                    <div class="operation">
                      <a class="items-delete-btn" @click="deleteCartItemHandle(item)"></a>
                    </div>
                    <div class="subtotal">¥ {{item.price*item.count}}</div>
                    <div class="item-cols-num">
                      <div class="select js-select-quantity">
                        <span class="down" :class="{'down-disabled':item.count===1}" @click="deItemHandle(item)">-</span>
                        <span class="num">{{item.count}}</span>
                        <span class="up" :class="{'up-disabled':item.count===5}" @click="addItemHandle(item)">+</span>
                      </div>
                    </div>
                    <div class="price">¥ {{item.price}}</div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div class="cart-bottom-bg fix-bottom" v-if="cartItem.length">
          <div class="cart-bar-operation">
            <div>
              <div class="choose-all js-choose-all">
                <span class="blue-checkbox-new" :class="{'checkbox-on':this.$store.getters.checkSelectAll}" @click="setSelectAllHandle">
                  <a></a>
                </span>
                全选
              </div>
              <div class="delete-choose-goods" @click="deleteSelectItemHandle">删除选中的商品</div>
            </div>
          </div>
          <div class="shipping">
            <div class="shipping-box">
              <div class="shipping-total shipping-num">
                <h4 class>
                  已选择
                  <i>{{this.$store.getters.selectCount}}</i> 件商品
                </h4>
                <h5>
                  共计
                  <i>{{this.$store.getters.totalCount}}</i> 件商品
                </h5>
              </div>
              <div class="shipping-total shipping-price">
                <h4 class>
                  应付总额：
                  <span>￥</span>
                  <i>{{this.$store.getters.selectPrice}}</i>
                </h4>
                <h5 class="shipping-tips">应付总额不含运费</h5>
              </div>
            </div>
            <span class="jianguo-blue-main-btn big-main-btn js-checkout" :class="{'disabled-btn':this.$store.getters.selectCount === 0}">
              <router-link :to="{name: 'CheckOut'}">现在结算</router-link>
            </span>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  computed: {
    cartItem () {
      return this.$store.state.cartPanelItem
    }
  },
  methods: {
    deleteCartItemHandle (item) {
      this.$store.commit('deleteCartItem', item)
    },
    addItemHandle (item) {
      if (item.count < 5) {
        this.$store.commit('addItem', {info: item, count: 1})
      }
    },
    deItemHandle (item) {
      if (item.count > 1) {
        this.$store.commit('deItem', item)
      }
    },
    changeSelectHandle (item) {
      this.$store.commit('changeSelect', item)
    },
    setSelectAllHandle () {
      this.$store.commit('setSelectAll')
    },
    deleteSelectItemHandle () {
      this.$store.commit('deleteSelectItem')
    }
  },
  created () {
    this.$store.commit('setSelect')
  }
}
</script>

<style>
@import url(../assets/css/cart.css);
</style>



